export default {
	created(){
		console.log()
	},
	props:{
		colors:{
			type:Array,
			default:()=>{
				return ['#fff','#fff']
			}
		},
		active:{
			type:Boolean,
			default:()=>{
				return false
			}
		}
	},
	computed:{
		gradientColor(){
			const colorBottom = `color-stop(30%, ${this.colors[0]})`
			const colorTop = `to(${this.colors[1]})`
			return `-webkit-gradient(linear, left bottom, left top, ${colorBottom}, ${colorTop})`
		}
	}
}
